const getEndpoint = file => file
  .replace(/http(s?)---/, 'http$1://')
  .replace(/-/g, '/')
  .replace('.ttl', '');

const getIdentifier = f => f.replace(/^.*?-(\w)/, '$1');

const configs = files.map(([f, size]) => [`/samples/${f}`, getEndpoint(f), `${size} MB`, getIdentifier(f)]);

export default configs;
